.everything{
    background-color: antiquewhite;
    padding: 6% 5%;
    font-size: 1.2rem;
    margin-bottom: -3rem;
    border: 1px solid black;
} 
.f-part button{
    width: 8rem;
    padding: 10px 5px;
}
.middleman{
    margin-top: 5%;
}
.middleman textarea{
    width: 100%;
    border: 1px solid black;
}
.middleman button{
    width: 10rem;
  height: 50px;
  background: rgb(15, 15, 15);
  transition-property: width;
  transition-duration: 2s;
  transition-timing-function: linear;
  transition-delay: 1s;
  font-size: 1.3rem;
  color: white;
}
.middleman button:hover{
    width: 300px;
}
input{
    padding: 15px 10px;
    width: 17rem;
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    border: 1px solid black;
}
form{
    display: flex;
    justify-content: space-between;
}
.down{
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
    padding: 10px 30px;
}
.logo img{
    width: 300px;
    height: 250px;
}
.logo p{
    width: 50%;
}
.down-sec h2{
    font-size: 2rem;
}
.down-sec p span{
    color: blue;
    font-size: 15px;
}
.down-sec p{
    border-bottom: 2px solid black;
}
a{
    text-decoration: none;
}
@media (min-width: 601px) and (max-width: 1024px){
    form{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .everything{
        margin-top: 20px;
    }
    textarea{
        width: 100%;
        margin-top: 3rem;
    }
}

@media (max-width: 600px) {
    .everything{
        margin-top: 30px;
    }
    textarea{
        width: 80%;
        margin-top: 2rem;
    }
    form{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .logo img{
        width: 300px;
        height: 250px;
        display: flex;
        align-self: center;
    }
    .down{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .logo p{
        width: 100%;
        font-size: 1.3rem;
    }
    .down-sec p{
       font-size: 1.2rem;
    }
    .down-sec p span{
        font-size: 1.4rem;
    }
}